body {
  background-color: #f0f2f5;
}

a {
  color: #000;
  text-decoration: none;
}

.avatar {
  vertical-align: middle;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.user-name {
  margin-bottom: 0;
  font-weight: 600;
}

.user {
  cursor: pointer;
}

.icon-flag {
  color: green;
}

.sign-out {
  color: red;
  background: none;
  border: 0;
  outline: none;
  font-weight: 700;
}

.sign-out i {
  font-size: 1.4rem;
}

.bi-heart {
  cursor: pointer;
  font-size: 1.4rem;
}

.bi-heart-fill {
  color: red;
  cursor: pointer;
  font-size: 1.4rem;
}

.profile-main {
  height: 100%;
  border-radius: 30px;
  flex-direction: column;
  display: flex;
  position: relative;
}

.avatar-profile {
  position: absolute;
  top: 50px;
  left: 30px;
}

.avatar-profile img {
  width: 100px;
  border-radius: 50%;
}

.main {
  width: 100%;
  height: 300px;
  margin-top: 1rem;
}

.info {
  height: 70%;
  background-color: #f0f8ff;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
}

.bg-profile {
  height: 40%;
  background-color: beige;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.info h3 {
  margin-top: 60px;
  margin-left: 1rem;
}

.hero {
  display: flex;
}

.img-collection {
  width: 35%;
  margin-top: 1rem;
}

.img-container {
  background-color: #fff;
  border-radius: 10px;
  padding: 1rem;
}

.img-container img {
  width: 110px;
  object-fit: cover;
  object-position: center;
  border-radius: 8px;
  margin-top: .6rem;
}

.posts-container {
  margin-top: 1rem;
}

.img-content {
  flex-wrap: wrap;
  gap: 1rem;
  display: flex;
}

.delete-btn {
  position: absolute;
  top: 30px;
  right: 20px;
}

.delete-btn span {
  cursor: pointer;
  color: #fff;
  background-color: red;
  border-radius: 8px;
  padding: .8rem 1rem;
  font-weight: 600;
}

